import * as React from 'react';
import { Row, Col } from "antd";
import { DivWithLeftMargin, DonationContainer } from './styles';
import { Slide } from 'react-awesome-reveal';
import Block from '../Block';

const Donate = ({title, content, id}) => {
    return (
        <DonationContainer id={id}>
            <Row justify={"space-between"} align={"middle"}>
                <Col lg={12} md={12} sm={24} xs={24}>
                    <Slide direction='left' triggerOnce>
                        <DivWithLeftMargin>
                            <stripe-buy-button
                                buy-button-id="buy_btn_1PwIQYRxzF3XYr6InCstLarI"
                                publishable-key="pk_test_51PrF6aRxzF3XYr6I539lJURyZDp1NYOcQvxicld10KckilXDjFHxunR5ifOGXVPgVz1ajDC4M3PlRIupF8CkyCOk00K8yE9Mgn"
                            >
                            </stripe-buy-button>
                        </DivWithLeftMargin>
                    </Slide>

                </Col>
                <Col lg={12} md={11} sm={24} xs={24}>
                <Slide direction='right' triggerOnce>
                <Block title="Donate here" content="Your donation helps people." />
                </Slide>
                    
                </Col>
            </Row>
        </DonationContainer>
    );
}

export default Donate;